
html {
  height: 100%;
}
body {
  min-width: 1100px;
  height: 100%;
  margin: 0;
  background: #f8f8f8;
}
#app {
  height: 100%;
  min-height: 800px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  overflow: hidden;
}

.resume {
  display: block;
  height: 100%;
}
@font-face {
  font-family: wbd;
  src: url(../../static/fonts/iconfont.0cd9fc3.woff);
}
.resume {
  height: 96%;
  margin: 1% auto;
  max-width: 1000px;
  border: 3px solid #264b6b;
  border-radius: 5px;
  -webkit-box-shadow: rgba(0, 0, 0, 0.1) 0 0 20px 8px;
          box-shadow: rgba(0, 0, 0, 0.1) 0 0 20px 8px;
  overflow: hidden;
}
.resume .part-left, .resume .part-right {
    height: 100%;
    padding: 25px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: scroll;
    text-align: left;
}
.resume .part-left .icon:hover, .resume .part-right .icon:hover {
      opacity: 0.8;
}
.resume .part-left .icon, .resume .part-left p, .resume .part-right .icon, .resume .part-right p {
      cursor: pointer;
}
.resume .part-left {
    float: left;
    width: 25%;
    height: 100%;
    background-image: radial-gradient(circle at 50% top, #285177, #254664);
}
.resume .part-left .avatar {
      position: relative;
      width: 180px;
      height: 180px;
      margin: 0 auto;
      background: #e5e5e5;
      border: 4px solid #e5e5e5;
      overflow: hidden;
      cursor: pointer;
}
.resume .part-left .avatar img {
        width: 100%;
        height: 100%;
}
.resume .part-left .personal {
      padding: 15px 40px 0;
      text-align: left;
      color: #fff;
}
.resume .part-left .personal a {
        text-decoration: none;
        color: #fff;
}
.resume .part-left .personal .icon {
        font-family: wbd, serif;
        margin-right: 8px;
}
.resume .part-left .personal .title .icon {
        float: left;
        display: inline-block;
        width: 30px;
        height: 30px;
        line-height: 32px;
        margin-right: 8px;
        border-radius: 50%;
        background: #fff;
        color: #254665;
        text-align: center;
}
.resume .part-left .personal .title .text {
        display: inline-block;
        position: relative;
        top: 3px;
        font-size: 18px;
        font-weight: bold;
        overflow: hidden;
        word-break: break-all;
}
.resume .part-left .personal .skill p {
        margin: 3px 0;
}
.resume .part-left .personal .skill .slider {
        display: inline-block;
        width: 125px;
        height: 10px;
        background: #f4f4f4;
}
.resume .part-left .personal .skill .slider .percent {
          display: block;
          width: 100%;
          height: 100%;
          background: #4487c6;
}
.resume .part-left .personal .interest {
        margin: 5px 0;
}
.resume .part-right {
    float: right;
    width: 75%;
    padding-left: 20px;
}
.resume .part-right .name {
      display: inline-block;
      height: 30px;
      line-height: 29px;
      padding: 10px 30px 0;
      font-size: 30px;
      color: #414141;
}
.resume .part-right .remark {
      position: relative;
      left: -16px;
      font-size: 10px;
      color: #808080;
}
.resume .part-right .intro {
      padding: 8px 30px 8px;
}
.resume .part-right .intro .title {
        position: relative;
        left: -8px;
        margin-top: 10px;
        margin-bottom: 10px;
}
.resume .part-right .intro .title .icon {
          display: inline-block;
          width: 40px;
          height: 40px;
          font-size: 24px;
          line-height: 40px;
          margin-right: 10px;
          color: #fff;
          background: #254665;
          border-radius: 50%;
          font-family: wbd, serif;
          text-align: center;
}
.resume .part-right .intro .title .text {
          display: inline-block;
          position: relative;
          top: 8px;
          width: 580px;
          height: 30px;
          padding-bottom: 4px;
          font-size: 24px;
          font-weight: bold;
          word-break: break-all;
          overflow: hidden;
          border-bottom: 1px solid #254665;
}
.resume .part-right .intro .content {
        width: 620px;
        margin-left: 5px;
        line-height: 30px;
        color: #4b4b4b;
        white-space: pre-wrap;
        word-break: break-all;
}
.resume .part-right .intro .content a {
          color: #2196f3;
          font-weight: bold;
          text-decoration: none;
}
.resume .part-right .intro .content i {
          font-style: normal;
}
.resume .part-right .intro .content h2 {
          font-size: 22px;
          margin: 24px 0 16px 0;
          color: #e91e63;
          border-bottom: 1px solid #ccc;
}
.resume .part-right .intro .content h2:before {
          content: '>> ';
}
.resume .part-right .intro .content h3 {
          display: table;
          width: auto;
          color: #2e7bff;
          font-size: 16px;
          font-weight: bold;
          border-bottom: 1px solid #2e7bff;
}
.resume .part-right .intro .content span {
          position: relative;
          top: -2px;
          left: 2px;
          font-size: 14px;
          color: #4487c6;
}
.resume .part-right .intro .content img, .resume .part-right .intro .content video {
          width: 100%;
          margin: 5px 0;
          border-radius: 5px;
          -webkit-box-shadow: rgba(0, 0, 0, 0.1) 0 0 20px 6px;
                  box-shadow: rgba(0, 0, 0, 0.1) 0 0 20px 6px;
}
.resume .part-right .intro .content img[svg] {
          width: auto;
          position: relative;
          top: 10px;
}
.resume .part-right .intro .content img[v] {
          position: relative;
          left: 25%;
          width: 50%;
}
.resume .part-right .intro .content img[vv] {
          display: inline-block;
          position: relative;
          margin: 0 2.5%;
          width: 45%;
          height: 450px;
}
.resume .part-right .intro .icon {
        font-family: wbd, serif;
}
